.ask-questions {
  position: relative;
  background-color: $DeepSkyBlue;
  overflow: hidden;
  display: flex;
  align-items: center;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(-40.8deg, transparent 66.4%, rgba(4, 24, 39, 0.5) 66.4%);
    background-blend-mode: overlay;

    @include vp-1023 {
      display: none;
    }
  }

  p {
    font-family: $ff-text;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-white;
    margin: 0 0 35px 0;


    @include vp-1023 {
      margin: 0 0 25px 0;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 19px;
      margin: 0 0 34px 0;
    }
  }
}

.ask-questions__inner {
  display: flex;
  align-items: center;
}

.ask-questions__banner {
  position: relative;
  top: -27px;
  width: 52%;
  z-index: 100;
}

.ask-questions__form-wrapper {
  position: relative;
  left: 35px;
  width: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 164px 0 250px 85px;

  @include vp-1023 {
    position: static;
    width: 100%;
    padding: 45px 0 125px;
  }

  @include vp-767 {
    width: 100%;
    padding: 47px 0 110px;
  }
}

.ask-questions__title {
  font-family: $ff-title;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;
  margin: 0 0 14px -5px;

  @include vp-1023 {
    margin: 0 0 8px 0;
  }

  @include vp-767 {
    margin: 0 0 7px 0;
    font-size: 30px;
    line-height: 42px;
  }
}

.ask-questions__picture {
  display: inline-block;
  background-color: $color-transparent;
}

.ask-questions__image {
  display: inline-block;
}
